@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');

body {
  font-family: 'Montserrat', sans-serif;
  background-color: #ffffff;
  color: #4a4a4a;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1, h2 {
  color: #4a4a4a;
}

.table {
  border-collapse: separate;
  border-spacing: 0;
  width: 100%;
  margin-top: 20px;
  border: 1px solid #d3d3d3;
  border-radius: 10px;
  overflow: hidden;
}

.table th, .table td {
  border: none;
  padding: 15px;
  text-align: center; /* Center align all table elements */
  vertical-align: middle; /* Ensure vertical alignment */
  font-family: 'Montserrat', sans-serif; /* Ensure font is applied to table elements */
}

.table th {
  background-color: #f8f9fa;
  font-weight: 700;
}

.table td {
  background-color: #ffffff;
  border-top: 1px solid #d3d3d3;
}

.table tr:nth-child(even) td {
  background-color: #f2f2f2; /* Alternate row color */
}

.table tr:last-child td {
  border-bottom: none;
}

.table .status-open, .table .status-closed {
  padding: 10px 20px;
  border-radius: 27px;
  display: inline-block;
  text-align: center;
  font-size: 16px; /* Ensure the font size is appropriate */
  line-height: 1.5; /* Ensure single line height */
  white-space: nowrap; /* Prevent text from wrapping */
  overflow: hidden;
}

.table .status-open {
  background-color: #28a745;
  color: #ffffff;
}

.table .status-closed {
  background-color: #dc3545;
  color: #ffffff;
}

.navbar {
  background-color: #4a4a4a;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-logo {
  height: 40px; /* Adjust the height as needed */
}

.navbar ul {
  list-style: none;
  display: flex;
  justify-content: space-around;
  padding: 0;
  margin: 0;
}

.navbar ul li {
  margin: 0 1rem;
}

.navbar ul li a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
}

.navbar ul li a:hover {
  text-decoration: underline;
}

.btn {
  border-radius: 27px; /* More rounded buttons */
  font-family: 'Montserrat', sans-serif; /* Ensure font is applied to buttons */
  padding: 10px 20px; /* Ensure same padding as status labels */
  text-align: center;
  font-size: 16px; /* Ensure the font size is appropriate */
  line-height: 1.5; /* Ensure single line height */
  white-space: nowrap; /* Prevent text from wrapping */
  overflow: hidden;
}

.btn-primary {
  background-color: #ff5e01;
  border-color: #ff5e01;
}

.btn-primary:hover {
  background-color: #e04e00;
  border-color: #e04e00;
}

.btn-secondary {
  background-color: #6c757d;
  border-color: #6c757d;
}

.btn-secondary:hover {
  background-color: #5a6268;
  border-color: #545b62;
}

.action-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px; /* Add space between buttons */
}

.pagination {
  display: flex;
  justify-content: center;
  padding: 0;
  list-style: none;
  margin-top: 20px;
}

.page-item {
  margin: 0 5px;
}

.page-link {
  color: #007bff;
  text-decoration: none;
  padding: 8px 12px;
  border: 1px solid #dee2e6;
  border-radius: 4px;
  font-family: 'Montserrat', sans-serif; /* Ensure font is applied to pagination */
}

.page-link:hover {
  background-color: #e9ecef;
  color: #0056b3;
  border-color: #dee2e6;
}
